.switch {
  width: 40px;
  height: 20px;
  cursor: pointer;
  display: inline-block;
  position: relative;
}

.switch-light, .switch-dark {
  display: none;
}

.switch__input {
  width: 0;
  height: 0;
  opacity: 0;
}

.switch__slider {
  background: #fff;
  border: .604701px solid #4b48db;
  border-radius: 20px;
  transition-duration: .3s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.switch__slider:before {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #4b48db;
  border-radius: 50%;
  transition-duration: .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.switch__input:checked + .switch__slider {
  background-color: #4b48db;
  border-color: #fff;
}

.switch__input:checked + .switch__slider:before {
  background-color: #fff;
  transform: translateX(20px);
}

@media screen and (min-width: 768px) {
  .switch-light, .switch-dark {
    display: none;
  }
}

